<template>
    <div>
        <form id="file_form" class="form-inline form-search text-center">
            <div class="form-group">
                <label></label>
                <v-select :value.sync="type" :options="types" placeholder="">
                </v-select>
                <label></label>
                <input type="text" class="form-control" onfocus="this.blur()" v-model="road">
                <input id="file" type="file" name="file" v-show="false" @change="changeFn">
                <button type="button" class="btn btn-default" @click="findFile"></button>
                <button type="button" class="btn btn-default" @click="uploadFile" :disabled="isLoading" v-text="isLoading ? '...' : ''"></button>
                <a type="button" class="btn btn-default" href="/file/template_cmdb.xlsx" target="_blank"></a>
            </div>
        </form>
        <div>
            <div class="import-msg">
                <p v-for="msg in msgs" v-text="msg"></p>
            </div>
        </div>
    </div>
</template>

<script>
import vSelect from '../../global/Select.vue'

export default {
    data () {
        return {
            isLoading: false,
            types: [
                {value: '1', label: ''},
                {value: '2', label: ''},
                {value: '3', label: ''},
                {value: '4', label: ''}
            ],
            type: '',
            road: '',
            msgs: ['', '1excel', '2']
        }
    },
    methods: {

        // 
        findFile () {
            $('#file').trigger('click')
        },

        // 
        changeFn () {
            this.road = $('#file').val()
        },

        // 
        uploadFile () {
            if (this.road !== '' && this.road.endsWith('.xlsx')) {
                let _this = this,
                    formData = new FormData($('#file_form')[0]),
                    url = ''

                // formData.append('type', _this.type)

                switch(this.type) {
                    case '1':
                        url = '/device/switch/import/'
                    case '2':
                        url = '/device/server/import/'
                        break
                    case '3':
                        url = '/node/importNode/'
                        break
                    case '4':
                        url = '/node/importIP/'
                        break
                }

                if (url) {
                    _this.isLoading = true

                    $.ajax({
                            url: url,
                            type: 'POST',
                            processData: false,
                            contentType: false,
                            dataType: 'JSON',
                            data: formData
                        })
                        .then(function (data) {
                            if (data.code === 200) {
                                _this.road = ''
                                $('#file').val('')
                                _this.msgs = ['', '1excel', '2']

                                _this.$dispatch('show-success', '')
                            } else {
                                _this.msgs = data.invalid

                                _this.$dispatch('show-error', data.msg)
                            }

                            _this.isLoading = false
                        })
                } else {
                    this.$dispatch('show-notify', '')
                }
            } else {
                this.$dispatch('show-notify', '.xlsx')
            }
        }
    },
    components: {
        vSelect
    }
}
</script>

<style scoped>
.import-msg {
    width: 600px;
    padding: 20px;
    margin: 150px auto 0;
    text-align: center;
    border: 1px solid #ccc;
}

.import-msg > p {
    color: #A04848;
}
</style>


